* {
    padding: 0;
    margin: 0;
}

#root, .choices {
    width: 100vw;
    height: 100vh;
}

.choice {
    width: 30vw;
    height: 30vw;
    color: black;
    background-color: gray;
    border-radius: 50%;
    position: absolute;
    bottom: 2vw;
}

    .choice#left {
        left: 2vw;
    }

    .choice#right {
        right: 2vw;
    }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }